import React, { useEffect,lazy,Suspense } from "react";
import Navbar from "./Navbar/Navbar";
import "./Home.css";
import HomeOne from "./Home_One";
// import HomeTwo from "./Home_Two";
// import HomeThree from "./Home_Three";
// import SocialMediaHandles from "../SocialMediaHandles/SocialMediaHandles";

// const HomeOne =lazy(()=>import("./Home_One"));
const HomeTwo =lazy(()=>import("./Home_Two"));
const HomeThree =lazy(()=>import("./Home_Three"));
const SocialMediaHandles =lazy(()=>import("../SocialMediaHandles/SocialMediaHandles"));


function Home() {
  useEffect(() => {
    document.title = "Home";
  }, []);

  return (
    <div className="home">
      <Navbar activeTab={"Home"} />
      <div className="home__body">
        <HomeOne />
        <Suspense fallback={<div>Please Wait....</div>}>
        <HomeTwo />
        <HomeThree />
        <SocialMediaHandles />
        </Suspense>
      </div>
    </div>
  );
}
export default Home;
